.Warrants {
	position: relative;
	width: 95%;
	left: 2.5%;
	height: 53vh;
	overflow-x: hidden;
	overflow-y: auto;

  &__title {
    font-size: 1.2vh;
  }

  &__search {
    position: relative;
    color: #444;
    font-size: 1.5vh;
    font-weight: bolder;
  }

  &__search-input {
    width: 25vh;
    padding: 0.5vh;

    color: #444;
    border: none;
    border-bottom: solid 2px #c9c9c9;
    outline: none;
    background-color: rgba(0, 0, 0, 0);

    &::placeholder {
      color: #444;
      font-weight: bolder;
    }
  }

  &__search-icon {
    padding: 1vh;
    transition: 0.3s;
    font-size: 2vh;
  }

  &__placeholder {
    margin-top: 13vh;
    text-align: center;
    font-size: 2vh;
  }

  &__list {
    margin-top: 10px;
  }
}

.Warrant {
    &:nth-child(odd) {
        padding: 1vh;
        border-top: solid 1px #444;
    }

    &:nth-child(even) {
        padding: 1vh;
        background-color: #c9c9c9;
        border-top: solid 1px #444;
    }

    &:first-child {
        padding: 1vh;
        border: none;
    }

    &:hover {
        background-color: rgba(0, 0, 0, 0.1);
    }

    &__offender {
        font-weight: bold;
    }

    &__icon {
        position: relative;
        float: right;
        top: 1.5vh;
        right: 1vh;
        font-size: 2vh;
    }

    &__details {
        font-size: 1.2vh;
    }


}

.WarrantView {
  position: relative;
	width: 95%;
	left: 2.5%;
	height: 53vh;
	overflow-x: hidden;
	overflow-y: auto;
	z-index: 1;

  &__title {
    font-size: 1.2vh;
  }

  &__header {
    font-size: 3vh;
    color: #444;
    font-weight: bold;
  }

  &__label {
    font-size: 1.5vh;
    font-weight: bolder;
  }

  &__mugshot {
    position: absolute;
    max-width: 14vh;
    margin: 1vh;
    right: 1vh;;
    opacity: 0.7;
  }

  &__grid {
    display: grid;

    margin-top: 1vh;
    padding: 1vh;

    color: #444;
    background-color: #c9c9c9;

    font-size: 1.3vh;

    grid-template-columns: 50% 50%;

    &-time {
      padding: 1vh;

      font-size: 1.5vh;

      grid-row: 1;
      grid-column: 1;
    }

    &-author {
      padding: 1vh;

      font-size: 1.5vh;

      grid-row: 1;
      grid-column: 2;
    }

    &-accused {
      padding: 1vh;

      font-size: 1.5vh;

      grid-row: 2;
      grid-column: 1;
    }

    &-id {
      padding: 1vh;

      font-size: 1.5vh;

      grid-row: 2;
      grid-column: 2;
    }

    &-details {
      padding: 1vh;

      font-size: 1.4vh;

      grid-row: 3;
      grid-column: 1 / 3;
    }

    &-charges {
      padding: 1vh;

      grid-row: 4;
      grid-column: 1 / 3;
    }

    &-total {
      padding: 1vh;

      font-size: 1.3vh;

      grid-row: 6;
      grid-column: 1;
    }

    &-outcome {
      padding: 1vh;

      font-size: 1.3vh;

      grid-row: 6;
      grid-column: 2;
    }

    &-add-charges {
      padding: 1vh;

      grid-row: 4;
      grid-column: 1 / 3;
    }

    &-selected-charges {
      padding: 1vh;

      font-size: 1.3vh;

      grid-row: 5;
      grid-column: 1 / 3;
    }
  }

  &__report {
    width: 20vh;
    margin: 0.5vh;
    height: 6vh;
    color: white;
    position: relative;
    display: inline-block;
    background-color: rgb(33,37,52);
    font-size: 1.2vh;
    padding: 1vh;
    transition: 0.3s;

    &:hover {
        background-color: #444;
        color: #d4d4d4;
    }
  }
}

